var token=""
import helper from '../../common/utils.js';
export default {
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			goodsList: [],
			nocStr:"",
			menus:[], 
		};
	},
	onLoad(option) {
		console.log("onLoad")
		//邀请ID
		if(option.id)
		{
			console.log(option.id)
			helper.invitaId = option.id
		}
	},

	onShow() {
		token = uni.getStorageSync("token")
		this.loadGoods();
		this.loadBanner();
		this.loadNoc();
		this.categryData();
	},
	methods: {
		
		loadBanner()
		{
			var _this = this
			uni.request({
			    url:this.$url+'/goods/coverImg/3' ,//服务器端地址
			    data: {
					
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.carouselList = res.data.data,
						_this.swiperLength = _this.carouselList.length
			        }
			    }
			   
			});
		},
		loadGoods()
		{
			var _this = this
			uni.request({
			    url:this.$url+'/goods/recommend' ,//服务器端地址
			    data: {
					pageNum:"0",
					pageSize:"6"
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.goodsList = res.data.data.content
			        }
			    }
			   
			});
		},
		loadNoc()
		{
			var _this = this
			uni.request({
			    url:this.$url+'/user/notice' ,//服务器端地址
			    data: {
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.nocStr = res.data.data
			        }
			    }
			   
			});
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		car(goodsId){
			var _this = this
			uni.request({
			    url:this.$url+'/car/add' ,//服务器端地址
			    data: {
					goodsId:goodsId
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						this.$api.msg('已添加购物车');
			        }
			    }
			   
			});
		},
		categryData(){
			var _this = this
			uni.request({
			    url:this.$url+'/goods/type/recommend' ,//服务器端地址
			    data: {
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.menus= res.data.data
						
			        }
			    }
			   
			});
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			console.log(item)
			uni.navigateTo({
				url: `/pages/product/product?id=${item}`
			})
		},
		//分类
		navToCatePage(item,pId) {
			//测试数据没有写id，用title代替
			console.log(item)
			console.log(pId)
			helper.cateId = item
			helper.catePid = pId
			uni.switchTab({
				url: '/pages/category/category'
			})
		},
		serGoods()
		{
			console.log("点击了")
			uni.navigateTo({
				url: `/pages/search/search`
			})
		}
	},
	
	
}
